.boxed-btn {
    background: #fff;
    color: $theme-color5;
    display: inline-block;
    padding: 18px 44px;
    font-family: $font1;
    font-size: 14px;
    font-weight: 400;
    border: 0;
    border: 1px solid $theme-color5;
    letter-spacing: 3px;
    // width: 180px;
    text-align: center;
    color: $theme-color5 !important;
    text-transform: uppercase;
    cursor: pointer;

    &:hover {
        background: $theme-color5;
        color: #fff !important;
        border: 1px solid $theme-color5;
    }

    &:focus {
        outline: none;
    }

    &.large-width {
        width: 220px;
    }
}

