    }
}

.line-button {
    color: #919191;
    font-size: 16px;
    font-weight: 400;
    display: inline-block;
    position: relative;
    padding-right: 5px;
    padding-bottom: 2px;

    &::before {
        position: absolute;
        content: "";
        background: #919191;
        width: 100%;
        height: 1px;
        bottom: 0;
        left: 0;
    }

    &:hover {
        color: #009DFF;
    }

    &:hover::before {
        background: #009DFF;
    }
}

.book_now {
    display: inline-block;
    font-size: 14px;
    color: #009DFF;
    border: 1px solid #009DFF;
    text-transform: capitalize;
    padding: 10px 25px;

    &:hover {
        background: #009DFF;
        color: #fff;
    }
}

.boxed_btn {
    cursor: pointer;
    font-size: 16px;
    color: #fff;
    padding: 17px 50px;
    background: #04D2C8;
    @include border-radius(30px);
    border: 1px solid transparent;
    display: inline-block;
    @include transition(.3s);

    &:hover {
        background: transparent;
        color: #04D2C8;
        border: 1px solid #04D2C8;
        color: #04D2C8 !important;
    }
}

